'use strict';

import Helper from '../Helper';

/**
 *
 * @type {Object}
 */
export default class Obj {
	/**
	 *       .
	 * @param {Object} targetObj -  .    .
	 * @param {Object} sourceObj - -.    .
	 * @param {Boolean} isRecursive - ,     -.
	 *  false,     (  , ..    ).
	 *  true,    targetObj     sourceObj.
	 */
	static apply(targetObj, sourceObj, isRecursive) {
		isRecursive = isRecursive || false;
		for (let key in sourceObj) {
			if (Helper.isObject(sourceObj[key])) {
				if (isRecursive) {
					Helper.Obj.apply(targetObj[key], sourceObj[key]);
				} else {
					targetObj[key] = Object.assign({}, sourceObj[key]);
				}
				continue;
			}
			targetObj[key] = sourceObj[key];
		}
	}

	/**
	 *    targetObj   sourceObj.
	 *       targetObj.
	 *      targetObj    isStrict,   .
	 * @param targetObj
	 * @param sourceObj
	 * @param isStrict
	 */
	static replaceMembers(targetObj, sourceObj, isStrict) {
		isStrict = isStrict === undefined ? true : isStrict;
		for (let key in sourceObj) {
			if (key in targetObj) {
				targetObj[key] = sourceObj[key];
			} else if (isStrict) {
				throw new Error(' "' + key + '"     ');
			}
		}
		return targetObj;
	}

	/**
	 *       ,     "arrOfObject".
	 * @param arrOfObject
	 * @param findFieldName
	 * @return {Array}
	 */
	static getFieldsAsArray(arrOfObject, findFieldName) {
		let result = [];
		arrOfObject.forEach((obj) => {
			if (!Helper.isObject(obj)) {
				return;
			}
			if (findFieldName in obj) {
				result.push(obj[findFieldName]);
			}
		});
		return result;
	};

	/**
	 *     "arrOfObject" 1 ,    "findFieldName"
	 *   "findFieldValue".
	 *  ,   .
	 *   ,  "defValue"
	 * @param arrOfObject
	 * @param findFieldName
	 * @param findFieldValue
	 * @param defValue
	 * @return {*}
	 */
	static getObjectFromArray(arrOfObject, findFieldName, findFieldValue, defValue = undefined) {
		for (let i = 0, arrLength = arrOfObject.length; i < arrLength; i++) {
			if (arrOfObject[i][findFieldName] === findFieldValue) {
				return arrOfObject[i];
			}
		}
		return defValue;
	};
};
